\t\t\t\t<pre>{{ systemInfo }}</pre>\n\t\t\t</p>\n\t\t</div>\n\t</div>\n</template>\n\n<script>\nimport axios from '@nextcloud/axios'\nimport { generateUrl } from '@nextcloud/router'\nimport { showSuccess } from '@nextcloud/dialogs'\n\nexport default {\n\tname: 'BugReport',\n\tdata() {\n\t\treturn {\n\t\t\tsystemInfo: null,\n\t\t\tloading: false,\n\t\t}\n\t},\n\tmethods: {\n\t\tcollectSystemInfo() {\n\t\t\tthis.loading = true\n\t\t\taxios.get(generateUrl('/apps/cloud_py_api/api/v1/system-info')).then(res => {\n\t\t\t\tthis.systemInfo = res.data\n\t\t\t\tthis.loading = false\n\t\t\t}).catch(err => {\n\t\t\t\tconsole.debug(err)\n\t\t\t\tthis.loading = false\n\t\t\t})\n\t\t},\n\t\tcopySystemInfoToClipboard() {\n\t\t\tnavigator.clipboard.writeText(JSON.stringify(this.systemInfo, null, 2))\n\t\t\tshowSuccess(this.t('cloud_py_api', 'Copied to clipboard'))\n\t\t},\n\t},\n}\n</script>\n\n<style scoped>\n.bug-report {\n\tpadding: 20px;\n\tborder-radius: 5px;\n\tborder: 1px solid #ff1414;\n}\n</style>\n"],"sourceRoot":""}]);
